.swiper .swiper-pagination-bullet:not(:disabled):focus {
  border: 1px solid #1c3374;
}
.swiper .swiper-pagination-bullet:not(:disabled):focus svg {
  stroke: #000000;
}
.swiper .swiper-pagination-bullet:not(:disabled):active {
  background-color: #1c3374;
  border-color: #1c3374;
}
.swiper .swiper-pagination-bullet:disabled {
  opacity: 10%;
  cursor: default;
}

.swiper .swiper-pagination-bullet-active {
  background: #ffffff;
}

.intro__slide-audio {
  position: absolute;
  width: fit-content;
  top: 301px;
  right: 0;
}
.intro__slide-audio iframe {
  width: 340px;
  height: 220px;
}

@media (max-width: 1199px) {
  .intro {
    width: 100%;
    min-height: 1024px;
  }
  .intro__slide {
    background-size: cover;
  }
  .intro__slide-container {
    position: relative;
    padding: 174px 30px 90px;
    max-width: 100%;
    margin: 0;
    min-height: 568px;
  }
  .intro__slide--kamchatka .intro__slide-container::after {
    background-size: cover;
    width: 236px;
    height: 312px;
    top: inherit;
    bottom: 169px;
    right: 28%;
    transform: translate(28%, 0);
  }
  .intro__slide--kamchatka .intro__slide-container::before {
    background-size: cover;
    width: 266px;
    height: 351px;
    top: inherit;
    bottom: 183px;
    right: inherit;
    left: 33%;
    transform: translate(-33%, 0);
  }
  .intro__slide-text {
    margin: 0 auto;
    max-width: 670px;
    width: 100%;
    align-items: center;
  }
  .intro__slide-text h3 {
    font-size: 56px;
    line-height: 56px;
    margin-bottom: 15px;
    text-align: center;
  }
  .intro__slide-text p {
    text-align: center;
    max-width: 462px;
    margin-bottom: 25px;
  }
  .intro__slide-text a {
    min-width: 206px;
    margin-bottom: 568px;
  }
  .intro__slide-description {
    display: none;
  }
  .intro__slide-video {
    top: inherit;
    bottom: 218px;
    right: 50%;
    transform: translate(50%, 0);
    z-index: 1;
    width: 442px;
    min-height: 300px;
  }
  .swiper .intro__slider-pagination {
    margin-bottom: 0;
    width: 100%;
    max-width: 100%;
    text-align: center;
    justify-content: center;
    left: 0;
    bottom: 90px;
    right: inherit;
    margin: 0 auto;
  }
  .intro__slide-audio {
    top: inherit;
    bottom: 279px;
    right: 50%;
    transform: translate(50%, 0);
  }
}
@media (max-width: 767px) {
  .intro {
    min-height: 568px;
  }
  .intro__slide-container {
    padding: 139px 15px 40px;
  }
  .intro__slide--kamchatka .intro__slide-container::after {
    display: none;
  }
  .intro__slide--kamchatka .intro__slide-container::before {
    display: none;
  }
  .intro__slide-text {
    margin: 0;
    max-width: 100%;
  }
  .intro__slide-text h3 {
    font-size: 30px;
    line-height: 30px;
  }
  .intro__slide-text p {
    max-width: 100%;
    margin-bottom: 20px;
    font-size: 15px;
    line-height: 20px;
  }
  .intro__slide-text a {
    min-width: 180px;
    margin-bottom: 0;
    padding: 18px;
  }
  .intro__slide-video {
    display: none;
  }
  .swiper .intro__slider-pagination {
    bottom: 40px;
  }
  .swiper .swiper-pagination-bullet {
    width: 8px;
    height: 8px;
  }
  .intro__slide-audio {
    display: none;
  }
}
